.navbar-nav > li,
.navbar-brand {
    /*padding: 30px 15px;*/
    -webkit-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out;
}
.navbar-default .navbar-brand, .navbar-light .navbar-brand {
    font-size: 22px;
}
