.contact form .btn {
    color: #fff;
    border: 2px solid transparent;
    line-height: 1;
    background: #6b347d;
    font-weight: 400;
    font-size: 13px;
    text-transform: uppercase;
    border-radius: 0;
    width: 150px;
    padding-top: 16px;
    padding-bottom: 16px;
    margin-top: 10px;
    -webkit-transition: all ease-in-out 0.2s;
